@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../../fonts/RobotoCondensed-Regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../../fonts/RobotoCondensed-Bold.woff") format("woff");
}
@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../../fonts/PTMono-Regular.woff2") format("woff2"), url("../../fonts/PTMono-Regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.clear-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.page__body p {
  font-family: "PT Mono", "Arial", sans-serif;
}

.page__body a {
  font-family: "PT Mono", "Arial", sans-serif;
}

.header__container {
  display: flex;
  background: #041827;
  min-height: 120px;
  position: relative;
}
@media (max-width: 768px) {
  .header__container {
    min-height: 70px;
  }
}

.header__container::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}

.header__wrapper-logo {
  z-index: 2;
  padding-top: 30px;
  padding-right: 160px;
  padding-left: 310px;
  position: relative;
}
@media (max-width: 1680px) {
  .header__wrapper-logo {
    padding-left: 260px;
  }
}
@media (max-width: 1600px) {
  .header__wrapper-logo {
    padding-left: 200px;
    padding-right: 100px;
  }
}
@media (max-width: 1440px) {
  .header__wrapper-logo {
    padding-left: 150px;
    padding-right: 80px;
  }
}
@media (max-width: 1280px) {
  .header__wrapper-logo {
    padding-left: 70px;
    padding-right: 30px;
  }
}
@media (max-width: 1024px) {
  .header__wrapper-logo {
    padding-left: 50px;
  }
}
@media (max-width: 768px) {
  .header__wrapper-logo {
    padding-left: 9px;
    padding-top: 15px;
  }
}

.nav {
  z-index: 2;
  position: relative;
  display: flex;
}
@media (max-width: 1024px) {
  .nav {
    margin-left: auto;
    padding-right: 50px;
  }
}
@media (max-width: 768px) {
  .nav {
    margin-left: auto;
    padding-right: 18px;
  }
}

@media (max-width: 1024px) {
  .nav__item span {
    display: none;
  }
}

.nav__list {
  padding-top: 45px;
  display: grid;
  grid-template-columns: 225px 225px;
  column-gap: 30px;
  row-gap: 20px;
}
@media (max-width: 1280px) {
  .nav__list {
    column-gap: 5px;
  }
}
@media (max-width: 1024px) {
  .nav__list {
    grid-template-columns: 40px 40px;
    column-gap: 10px;
    row-gap: 10px;
    padding-top: 35px;
  }
}
@media (max-width: 768px) {
  .nav__list {
    padding-top: 10px;
  }
}

.nav__item a {
  display: flex;
  font-size: 16px;
  line-height: 18px;
  color: #F2F5F7;
}
@media (max-width: 1024px) {
  .nav__item a {
    padding-top: 10px;
    padding-bottom: 10px;
  }
}

.nav__icon {
  display: block;
  margin-right: 15px;
}
@media (max-width: 1024px) {
  .nav__icon {
    margin: auto;
  }
}

.nav__button {
  font-family: "Roboto Condensed";
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  margin-top: 35px;
  margin-left: 142px;
  width: 160px;
  height: 40px;
  border: 1px solid #F2F5F7;
  border-radius: 3px;
  background-color: rgba(255, 255, 255, 0);
  color: #F2F5F7;
}
@media (max-width: 1600px) {
  .nav__button {
    margin-left: 100px;
  }
}
@media (max-width: 1440px) {
  .nav__button {
    margin-left: 70px;
  }
}
@media (max-width: 1280px) {
  .nav__button {
    margin-left: 40px;
  }
}
@media (max-width: 1024px) {
  .nav__button {
